.area__setup {
}
.area__setup__title {
    text-align: center;
    color: #d63031;
    padding: 15px 0;
}
.area__setup__container {
    border-bottom: 2px solid #3a3a3a;
    padding: 50px 0;
}
.area__setup__container:last-child {
    border-bottom: none;
}
.area__setup__form {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.area__setup__select {
    border: 2px solid #333;
    padding: 1px 10px;
    width: 280px;
    border-radius: 4px;
}
.area__setup__select__inline {
    border: 2px solid #333;
    padding: 1px 10px;
    border-radius: 4px;
}
.area__setup__input {
    display: block;
    border: 2px solid #333;
    padding: 3px 10px;
    width: 280px;
    border-radius: 4px;
}
.area__setup__level {
    text-align: right;
    margin-bottom: -24px;
}
.area__setup__level span {
    background-color: #39b54a;
    color: #fff;
    padding: 1px 17px;
    font-size: 14px;
}
.area__setup__save__btn {
    border: none;
    padding: 1px 12px;
    background: #39b54a;
    color: #fff;
    margin: 12px 0;
}
.area__setup__area__list {
}
.area__setup__area__list__item {
    display: flex;
    align-items: center;
}
.area__setup__name__container {
    background: #d8d8d8;
    padding-left: 20px;
    margin-bottom: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
.area__setup__name {
    margin-bottom: 0;
    font-size: 19px;
}
.area__setup__action__btn {
    border: none;
    padding: 4px 10px;
    color: #fff;
    background: #39b54a;
    font-size: 14px;
    margin-right: 1px;
}
.area__setup__select {
    border: 2px solid #333;
    padding: 4px 10px;
    width: 280px;
    border-radius: 4px;
}

@media (max-width: 575.98px) {
    .area__setup__level {
        margin-bottom: 10px;
    }
    .area__setup__name {
        font-size: 16px;
    }
    .area__setup__action__btn {
        font-size: 12px;
    }
    .area__setup__name__container {
        padding-left: 5px;
    }
    .area__setup__select {
        width: 230px;
    }
    .area__setup__input {
        width: 230px;
    }
    .area__setup__select__area {
        padding: 0;
        margin-top: 43px;
    }
    .area__setup__select__inline {
        width: 90%;
        margin-left: 13px;
    }
}

